import { useState, useEffect, useRef } from "react";
import "./Rocket.scss"; // Создайте файл RocketAnimation.css

import rocket from "../../../img/svgicons/rocket.svg";
import fire from "../../../img/svgicons/fire/1.svg";
import { useDispatch } from "react-redux";
import { setTotalRocketCoins, setTotalClicks } from "../../../redux/rocket";

export default function Rocket({ totalClicksCount, isTurbo }) {
  const containerRef = useRef(null);
  const [stars, setStars] = useState([]);

  const dispatch = useDispatch();

  useEffect(() => {
    const containerHeight = containerRef.current.clientHeight;

    const createStar = () => {
      const x = Math.random() * containerRef.current.clientWidth;
      const size = Math.random() * 2 + 1; // Размер от 1 до 3 пикселей
      return { x, size, y: -size }; // Убираем width и height из объекта звезды
    };

    // Добавляем начальные звезды
    for (let i = 0; i < 2; i++) {
      // Уменьшено количество начальных звезд
      setStars((prevStars) => [...prevStars, createStar()]);
    }

    const animationInterval = setInterval(() => {
      setStars((prevStars) => {
        return prevStars.map((star) => {
          const newY = star.y + 1; // Скорость падения
          if (newY > containerHeight) {
            return createStar(); // Создаем новую звезду на верхней границе
          } else {
            return { ...star, y: newY };
          }
        });
      });
    }, 50); // 50 мс - частота анимации

    const starSpawnInterval = setInterval(() => {
      stars.length < 40 &&
        setStars((prevStars) => [...prevStars, createStar()]); // Добавляем новую звезду
    }, 1000); // Интервал появления новых звезд (1000 мс = 1 секунда)

    return () => {
      clearInterval(animationInterval);
      clearInterval(starSpawnInterval); // Очищаем интервал появления звезд
    };
  }, []);

  const rocketHandleClick = () => {
    if (!totalClicksCount || isTurbo) {
      return;
    }

    dispatch(setTotalRocketCoins("click"));
    dispatch(setTotalClicks("click"));
  };

  return (
    <div className="rocket-container" ref={containerRef}>
      <div className="rocket" onClick={rocketHandleClick}>
        <div className="rocket-body">
          <img src={rocket} alt="" />
        </div>
        <div className="rocket-fire">
          <img src={fire} alt="" />
        </div>
      </div>
      <div className="stars">
        {stars.map((star, index) => (
          <div
            key={index}
            className="star"
            style={{
              left: `${star.x}px`,
              width: `${star.size}px`,
              height: `${star.size}px`,
              top: `${star.y}px`,
            }}
          ></div>
        ))}
      </div>
    </div>
  );
}
