import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
const initialState = {
  turbo: {
    total: 3,
    max: 3,
  },
  maxClickToday: 1000,
  turboClicks: 20,
  rocketCoins: {
    total: 0,
    max: 1500000,
  },
};

export const rocketSlice = createSlice({
  name: "rocket",
  initialState,
  reducers: {
    setTotalRocketCoins: (state, action) => {
      const newData = 1;
      state.rocketCoins = {
        ...state.rocketCoins,
        total: state.rocketCoins.total + newData,
      };
    },
    setTotalClicks: (state, action) => {
      --state.maxClickToday;
    },
    setTotalTurbo: (state) => {
      state.turbo.total--;
    },
  },
});

export const { setTotalRocketCoins, setTotalClicks, setTotalTurbo } =
  rocketSlice.actions;

export default rocketSlice.reducer;
