import "./Wallet.scss";

import Tabs from "../Common/Tabs/Tabs";

import logo from "../../img/svgicons/logo.svg";
import turbo from "../../img/svgicons/turbo.svg";
import ton from "../../img/svgicons/ton.svg";
import friends from "../../img/svgicons/friends-white.svg";
import invite from "../../img/svgicons/invite.svg";
import wheel from "../../img/svgicons/wheel.svg";

import walletImg from "../../img/output/1.png";
import coinImg from "../../img/output/2.png";
import { useState } from "react";

export default function Wallet({}) {
  const [triesWheel, setTriesWheel] = useState(3);
  const [adWatched, setAdWatched] = useState(1);
  const tabsOutput = [
    <>
      <h2>Вывод денег</h2>
      <p>
        Подключите свой TON кошелек, чтобы выводить выгранные деньги с рейтинга,
        а так же с ускорения ракеты
      </p>
      <img src={walletImg} alt="wallet" />
      <button className="btn btn-blue">
        <img src={ton} alt="ton" />
        <div className="text">
          <p>Подключить свой TON-кошелек</p>
        </div>
      </button>
    </>,
  ];
  const tabsToken = [
    <>
      <h2>Листинг токена</h2>
      <section className="listing-clicks block-with-bg">
        <div className="listing-clicks__item blue">
          <div className="listing-clicks__img">
            <img src={turbo} alt="turbo" />
          </div>
          <div className="listing-clicks__info">
            <h3>Ваше количество кликов:</h3>
            <p>115 417</p>
          </div>
        </div>
        <div className="listing-clicks__item pink">
          <div className="listing-clicks__img">
            <img src={turbo} alt="turbo" />
          </div>
          <div className="listing-clicks__info">
            <h3>Ваши токены</h3>
            <p>Клики будут учтены для airdrop! </p>
          </div>
        </div>
      </section>
      <img src={coinImg} alt="coinImg" />
      <section className="listing-stats">
        <div className="listing-stats__item blue">
          <div className="listing-stats__img">
            <img src={friends} alt="friends" />
          </div>
          <div className="listing-stats__info">
            <h4>Регистраций: 9 137 089</h4>
            <h3>Ежемесячных пользователей в игре (MAU):</h3>
            <p>5 232 667</p>
          </div>
        </div>
        <div className="listing-stats__item">
          <div className="listing-stats__img">
            <img src={friends} alt="friends" />
          </div>
          <div className="listing-stats__info">
            <h4>Регистраций: 9 137 089</h4>
            <h3>Ежемесячных пользователей в игре (MAU):</h3>
            <p>5 232 667</p>
          </div>
        </div>
        <div className="listing-stats__item">
          <div className="listing-stats__img">
            <img src={friends} alt="friends" />
          </div>
          <div className="listing-stats__info">
            <h4>Регистраций: 9 137 089</h4>
            <h3>Ежемесячных пользователей в игре (MAU):</h3>
            <p>5 232 667</p>
          </div>
        </div>
      </section>
      <section className="listing-invite">
        <div className="listing-invite__header">
          <img src={invite} alt="invite" />
          <h2>Пригласи друга, чтобы ускорить процесс!</h2>
        </div>
        <button className="btn btn-gradient">Пригласи Друга</button>
      </section>
    </>,
  ];
  const tabsLinks = ["Вывод денег", "Токен"];
  const tabsContent = [tabsOutput, tabsToken];

  const wheelWinners = [
    {
      name: "Иван Иванов",
      clicks: "223 856 кликов",
      prize: "20 TON",
    },
    {
      name: "Иван Иванов",
      clicks: "223 856 кликов",
      prize: "20 TON",
    },
    {
      name: "Иван Иванов",
      clicks: "223 856 кликов",
      prize: "20 TON",
    },
  ];
  return (
    <>
      <section className="block-white">
        <header className="wallet-header">
          <img src={logo} alt="logo" />
          <img src={ton} alt="ton" />
        </header>
        <Tabs tabsLinks={tabsLinks} tabsContent={tabsContent} />
      </section>
      <section className="block-white wheel">
        <h2>Беспроигрышное колесо удачи</h2>
        <p>
          Каждый день в 00:00 по UTC у вас появляется 1 шанс испытать
          беспроигрышное колесо удачи!
        </p>
        <section className="wheel__winners">
          {wheelWinners.map(({ name, clicks, prize }) => {
            return (
              <div className="wheel__winner">
                <div className="wheel__player">
                  <h3>{name}</h3>
                  <p>{clicks}</p>
                </div>
                <p className="wheel__prize">{prize}</p>
              </div>
            );
          })}
        </section>
        <img src={wheel} alt="" />
        <button class="btn btn-gradient">
          Играть <span>{triesWheel} попытки</span>
        </button>
        <section className="wheel-ad">
          <h3>Играть за рекламу</h3>
          <div className="wheel-ad__progress">
            <div
              className="progress-bar"
              style={{ width: adWatched / 0.03 + "%" }}
            />
            <div className="count">{adWatched}/3</div>
          </div>
        </section>
        <button className="btn-ad">
          <div className="btn-ad__indicator"></div>
          <div className="btn-ad__text">
            <h3>Реклама</h3>
            <p>Рекалмный текст</p>
          </div>
          <div className="btn-ad__ad">ad</div>
        </button>
      </section>
    </>
  );
}
