import "./Partner.scss";

import rocketCoin from "../../../img/svgicons/turbo.svg";
import coin from "../../../img/svgicons/coin-money.svg";

export default function Partner({ title, subTitle, partners }) {
  function createTask({
    img,
    partnerTitle,
    partnerSubtitle,
    partnerText,
    color = "purple",
    addClicks,
    addCoins,
  }) {
    return (
      <article className={`partner__partner ${color}`}>
        <header className="partner__partner-header">
          <aside className="partner__info">
            <div className="partner__img">
              <img src={img} alt="" />
            </div>
            <h3>{partnerTitle}</h3>
            <h4>{partnerSubtitle}</h4>
          </aside>
          <main className="partner__text">{partnerText}</main>
        </header>
        <div className="partner__clicks">
          <img src={rocketCoin} alt="rocketCoin" />
          <p>{addClicks}</p>
        </div>
        <div className="partner__coins">
          <img src={coin} alt="coin" />
          <p>{addCoins}</p>
        </div>
      </article>
    );
  }
  return (
    <section className="partner">
      <header className="partner__header">
        {title && <h2>{title}</h2>}
        {subTitle && <p>{subTitle}</p>}
      </header>
      {partners &&
        partners.map((partner) => {
          return createTask(partner);
        })}
      <footer className="partner__footer">
        «Полет в космос» не заставляет брать микрокредиты. Воспользуйтесь
        сервисом, если вы реально нуждаетесь в займе.
      </footer>
    </section>
  );
}
