import "./Rating.scss";

import TextHeader from "../Common/TextHeader/TextHeader";

export default function Rating({}) {
  const ratingPlaces = [
    { place: "1 место", payment: "3 TON" },
    { place: "2 место", payment: "1.5 TON" },
    { place: "3 место", payment: "0.8 TON" },
    { place: "4-10 место", payment: "0.2 TON" },
    { place: "11-50 место", payment: "0.1 TON" },
  ];
  const ratingPlayers = [
    {
      playerName: "Иван Иванов",
      countToday: "23542441",
      countAll: "992104429",
    },
    {
      playerName: "Иван Иванов",
      countToday: "23542441",
      countAll: "992104429",
    },
    {
      playerName: "Иван Иванов",
      countToday: "23542441",
      countAll: "992104429",
    },
    {
      playerName: "Иван Иванов",
      countToday: "23542441",
      countAll: "992104429",
    },
    {
      playerName: "Иван Иванов",
      countToday: "23542441",
      countAll: "992104429",
    },
  ];
  return (
    <section className="block-white">
      <TextHeader title={[<span>Рейтинг игроков </span>]} />
      <p>Каждый день в 20:00 ТОП-50 пользователей получают призы в TON</p>
      <article className="rating-places">
        {ratingPlaces.map(({ place, payment }) => {
          return (
            <div>
              <p>{place}</p>
              <p>{payment}</p>
            </div>
          );
        })}
      </article>
      <p>
        Топ дня обновляется ежедневно в 20:00 по Мск. Если вы до этого времени
        будете в ТОП-50, то у вас откроется возможность вывести деньги на свой
        TON кошелек.
      </p>
      <p>
        В 00:00 начинают учитываться новые клики для участия в ежедневном
        розыгрыше
      </p>
      <article className="rating-players">
        {ratingPlayers.map(({ playerName, countToday, countAll }, i) => {
          return (
            <div className="rating-players__player">
              <div className="rating-players__place">{++i}</div>
              <h3>{playerName}</h3>
              <div className="rating-players__count">
                <p>{countToday}</p>
                <p>{countAll}</p>
              </div>
            </div>
          );
        })}
      </article>
      <button className="btn-ad">
        <div className="btn-ad__indicator"></div>
        <div className="btn-ad__text">
          <h3>Реклама</h3>
          <p>Рекалмный текст</p>
        </div>
        <div className="btn-ad__ad">ad</div>
      </button>
    </section>
  );
}
