import "./Task.scss";

import star from "../../../img/svgicons/star.svg";
import google from "../../../img/svgicons/google.svg";

export default function Task({ title, subTitle, tasks }) {
  function createTask({
    taskType,
    taskTitle,
    taskText,
    isAd,
    isNew,
    color = "blue",
  }) {
    return (
      <article className={`task__task ${color}`}>
        <img src={taskType === "google" ? google : star} alt="" />
        <main className="task__text">
          <h3>{taskTitle}</h3>
          <p>{taskText}</p>
        </main>
        {isAd && <div className="task__ad">AD</div>}
        {isNew && <div className="task__new"></div>}
      </article>
    );
  }
  return (
    <section className="task">
      <header className="task__header">
        {title && <h2>{title}</h2>}
        {subTitle && <p>{subTitle}</p>}
      </header>
      {tasks &&
        tasks.map((task) => {
          return createTask(task);
        })}
    </section>
  );
}
