import "./SuperClick.scss";

import Tabs from "../Common/Tabs/Tabs";
import Task from "../Common/Task/Task";
import Partner from "../Common/Partner/Partner";

import loanImg from "../../img/loan/bg.png";
import partnerImg from "../../img/loan/more.png";
import sponsorLogo from "../../img/gameSport/logo.png";
import sponsorBg from "../../img/gameSport/bg.png";
import cancel from "../../img/svgicons/cancel.svg";
import coin from "../../img/svgicons/coin.svg";

export default function SuperClick({}) {
  const tabsOtherSubs = [
    <>
      <p>
        Компании ниже являются спонсорами нашего проекта “Полет в космос”.
        Оформляйте займ, если вы реально нуждаетесь в нем.
      </p>
      <Task
        title="Подписки"
        tasks={[
          {
            taskType: "google",
            taskTitle: "Выполни ежедневные задания через поиск Google",
            taskText: "+10 000 000 кликов",
            isNew: true,
          },
          {
            taskType: "google",
            taskTitle: "Выполни ежедневные задания через поиск Google",
            taskText: "+10 000 000 кликов",
            isNew: true,
          },
          {
            taskType: "star",
            taskTitle: "Выполни ежедневные задания через поиск Google",
            taskText: "+10 000 000 кликов",
          },
        ]}
      />
    </>,
  ];
  const tabsOtherServices = [
    <>
      <p>
        Компании ниже являются спонсорами нашего проекта “Полет в космос”.
        Оформляйте займ, если вы реально нуждаетесь в нем.
      </p>
      <Task
        title="Подписки"
        tasks={[
          {
            taskType: "google",
            taskTitle: "Выполни ежедневные задания через поиск Google",
            taskText: "+10 000 000 кликов",
            isNew: true,
            color: "purple",
          },
          {
            taskType: "google",
            taskTitle: "Выполни ежедневные задания через поиск Google",
            taskText: "+10 000 000 кликов",
            isNew: true,
            color: "purple",
          },
          {
            taskType: "star",
            taskTitle: "Выполни ежедневные задания через поиск Google",
            taskText: "+10 000 000 кликов",
            color: "purple",
          },
        ]}
      />
    </>,
  ];
  const tabsOtherPartners = [
    <>
      <p>
        Компании ниже являются спонсорами нашего проекта “Полет в космос”.
        Оформляйте займ, если вы реально нуждаетесь в нем.
      </p>
      <Task
        title="Подписки"
        tasks={[
          {
            taskType: "google",
            taskTitle: "Выполни ежедневные задания через поиск Google",
            taskText: "+10 000 000 кликов",
            isNew: true,
            color: "green",
          },
          {
            taskType: "google",
            taskTitle: "Выполни ежедневные задания через поиск Google",
            taskText: "+10 000 000 кликов",
            isNew: true,
            color: "green",
          },
          {
            taskType: "star",
            taskTitle: "Выполни ежедневные задания через поиск Google",
            taskText: "+10 000 000 кликов",
            color: "green",
          },
        ]}
      />
    </>,
  ];
  const tabsOtherTabs = [tabsOtherSubs, tabsOtherServices, tabsOtherPartners];
  const tabsOtherLinks = ["Подписки", "Сервисы", "Партнерские программы"];

  const tabsOther = [
    <section className="superclick-tabs">
      <Tabs tabsLinks={tabsOtherLinks} tabsContent={tabsOtherTabs} />
    </section>,
  ];
  const tabsLoan = [
    <>
      <header className="tabs-loan__header">
        <h2>Займ</h2>
        <p>Оформи займ за 15 минут и получите 3 000 000 кликов</p>
      </header>
      <img src={loanImg} alt="loanImg" />
      <p>
        Компании ниже являются спонсорами нашего проекта “Полет в космос”.
        Оформляйте займ, если вы реально нуждаетесь в нем.
      </p>
      <Partner
        title="Больше кликов"
        partners={[
          {
            img: partnerImg,
            partnerTitle: "Вебзайм",
            partnerSubtitle: "Процент одобрения 95%",
            partnerText: [
              <>
                <p>
                  Сумма кредита <span>от 3000₽ до 30000₽</span>
                </p>
                <p>
                  Срок кредита <span>от 7 до 30 дней</span>
                </p>
                <p>
                  Возраст <span>от 18 лет</span>
                </p>
                <p>
                  Ставка <span>от 0%</span>
                </p>
              </>,
            ],
            addClicks: "+ 3 000 000 кликов",
            addCoins: "+1",
          },
        ]}
      />
    </>,
  ];
  const tabsToken = [
    <div className="sponsor">
      <h2>GameSport</h2>
      <div className="sponsor-block">
        <div className="sponsor-block__img">
          <img src={sponsorLogo} alt="sponsorLogo" />
        </div>
        <p>GameSport официальный спонсор “Полет в космос”</p>
      </div>
      <div className="sponsor-bg">
        <img src={sponsorBg} alt="sponsorBg" />
      </div>
      <p>
        Получите доступ к аналитике киберспорта на 5 дней всего за 1₽ и начните
        зарабатывать от 150 000₽ в месяц.
      </p>
      <div className="sponsor-sale block-with-bg">5 дней = 1₽</div>
      <p>
        Оформите подписку на PRO-аналитику и получите дополнительные возможности
        от нашего спонсора.
      </p>
      <div className="sponsor-statement">
        <img src={cancel} alt="cancel" />
        <img src={coin} alt="coin" />
        <p>+ 1 000 000</p>
      </div>
      <button className="btn btn-gradient">Участвовать</button>
      <button className="btn-bordered btn-gradient">
        <p>Проверить участие</p>
      </button>
      <div className="sponsor-task red">Задание не выполнено</div>
    </div>,
  ];

  const tabsLinks = ["Разное", "Займ", "Токен"];
  const tabsContent = [tabsOther, tabsLoan, tabsToken];
  return (
    <section className="sponsor-tabs block-white">
      <Tabs
        tabsLinks={tabsLinks}
        tabsContent={tabsContent}
        tabsColor="purple"
      />
    </section>
  );
}
